.container {
  /*  Positioning */
  width: 100%;
  /* Spacing */
  padding: 20px 0;
  /* Styling */
  background-color: grey;
}

.search-bar {
  position: relative;
  padding: 0 20px;
}

.search-bar .icon {
  /* Positioning */
  position: absolute;
  top: 50%;
  left: 40px;
  transform: translateY(-50%);
  /* Styling */
  font-size: 36px;
  width: 36px;
  color: var(--mainColor);
}

.search-bar__input {
  /*  Positioning */
  width: 100%;
  height: 88px;
  /* Spacing */
  padding: 26px 20px 26px 72px;
  /* Styling */
  border: none;
  box-shadow: 0px 26px 30px rgba(0, 0, 0, 0.05);
  border-radius: 8px;
}

.search-bar__input:focus {
  outline: none;
}

.search-bar__input::placeholder {
  font-family: sans-serif;
  font-size: 18px;
  line-height: 28px;
}

.search-bar .times-icon {
  display: none;
}

.search-bar__input:not(:placeholder-shown) ~ .search-icon {
  display: none;
}

.search-bar__input:not(:placeholder-shown) ~ .times-icon {
  display: block;
}

.test {
  display: flex;
  flex-direction: column;
  justify-content: center;
}